/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
  -khtml-user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  display: block;
  overflow: hidden;
  margin: 0;
  padding: 0; }

.slick-list:focus {
  outline: none; }

.slick-list.dragging {
  cursor: pointer;
  cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  top: 0;
  left: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.slick-track:before,
.slick-track:after {
  display: table;
  content: ''; }

.slick-track:after {
  clear: both; }

.slick-loading .slick-track {
  visibility: hidden; }

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px; }

[dir='rtl'] .slick-slide {
  float: right; }

.slick-slide img {
  display: block; }

.slick-slide.slick-loading img {
  display: none; }

.slick-slide.dragging img {
  pointer-events: none; }

.slick-initialized .slick-slide {
  display: block; }

.slick-loading .slick-slide {
  visibility: hidden; }

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

.header {
  width: 1440px;
  height: 700px;
  background-image: url("../img/top-bg.png");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: contain;
  margin-bottom: 135px; }

.header-title {
  font-family: Georgia;
  font-style: normal;
  font-weight: bold;
  font-size: 200px;
  line-height: 227px;
  text-align: center;
  color: #FFFFFF;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.5);
  padding-top: 196px; }

.header-subtitle {
  font-family: Georgia;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 27px;
  text-align: center;
  color: #FFFFFF;
  text-shadow: 0px 2px 1px rgba(0, 0, 0, 0.5);
  padding-top: 90px; }

.nav-list {
  margin-top: 64px;
  display: flex;
  justify-content: space-between;
  padding: 0 400px; }

.nav-list__item a {
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  display: flex;
  align-items: center;
  color: #909090; }
  .nav-list__item a--active {
    color: #4E5665;
    border-bottom: 4px solid #ECB84C;
    padding-bottom: 3px; }
  .nav-list__item a:hover {
    color: #4E5665;
    border-bottom: 4px solid #ECB84C;
    padding-bottom: 16px; }

.statistic {
  width: 970px;
  margin: 0 auto; }

.statistic-title {
  font-family: Georgia;
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 38px;
  text-align: center;
  color: #4E5665;
  margin-bottom: 22px; }

.statistic-text {
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  align-items: center; }

.statistic-link {
  font-weight: bold;
  font-size: 16px;
  line-height: 28px;
  color: #ECB84C;
  margin-left: 860px; }

.advantages-list {
  display: flex;
  justify-content: space-between; }

.advantages-list__item:first-child {
  margin-left: 90px; }

.advantages-list__item {
  margin-right: 162px; }

.advantages-list__item-number {
  font-weight: bold;
  font-size: 22px;
  line-height: 30px; }

.advantages-list__item-text {
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #909090; }
